import Vue from "vue";
import Router from "vue-router";
import NProgress from "nprogress";
import axios from "axios";
import NotFound from "@/ticketing/views/NotFound";
import VueMeta from "vue-meta";

import store from "@/store/store";
import TicketService from "@/services/TicketService";
import ProfileService from "@/services/ProfileService";
Vue.use(Router);
Vue.use(VueMeta);
const router = new Router({
  // mode: "history",
  // base: "/events/05-2019/ticketing/",
  routes: [
    {
      path: "/:eventSlug/rsvp/closed",
      name: "rsvps-closed",
      component: () =>
        import(/* webpackChunkName: "rsvp" */ "@/ticketing/views/RSVPClosed"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getEvent(routeTo.params.eventSlug)
          .then(res => {
            routeTo.params.event = res.data.event;
            next();
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/rsvp",
      name: "rsvp-new-person",
      component: () =>
        import(/* webpackChunkName: "rsvp" */ "@/ticketing/views/RSVPs"),
      props: true,
      async beforeEnter(routeTo, routeFrom, next) {
        try {
          await Promise.all([
            store.dispatch("events/getEvent", routeTo.params.eventSlug),
            store.dispatch("people/getNewPerson"),
            store.dispatch("rsvps/getNewRSVP")
          ]);
          next();
        } catch (e) {
          next({ name: "404" });
        }
      }
    },
    {
      path: "/:eventSlug/rsvp/:personId",
      name: "rsvp",
      component: () =>
        import(/* webpackChunkName: "rsvp" */ "@/ticketing/views/RSVPs"),
      props: true,
      async beforeEnter(routeTo, routeFrom, next) {
        const eventSlug = routeTo.params.eventSlug;
        const personId = routeTo.params.personId;
        await Promise.all([
          store.dispatch("events/getEvent", eventSlug),
          store.dispatch("people/getPerson", personId)
        ]);
        const event = store.getters["events/event"];
        if (event && !event.hasRsvps) {
          next({
            name: "404",
            params: { message: "This event doesn't have RSVP's enabled." }
          });
        } else {
          await store.dispatch("rsvps/getRSVP", { eventSlug, personId });
          next();
        }
      }
    },
    {
      path: "/:eventSlug/apply",
      name: "apply",
      component: () =>
        import(/* webpackChunkName: "apply" */ "@/ticketing/views/Applications"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        store.dispatch("people/getNewPerson");
        store
          .dispatch("events/getEvent", routeTo.params.eventSlug)
          .then(res => {
            next();
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/apply/:id",
      name: "application-status",
      component: () =>
        import(/* webpackChunkName: "application-status" */ "@/ticketing/views/ApplicationStatus"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        axios
          .all([
            TicketService.getEvent(routeTo.params.eventSlug),
            TicketService.getApplication(
              routeTo.params.eventSlug,
              routeTo.params.id
            )
          ])
          .then(
            axios.spread((eventRes, ticketOfferRes) => {
              const application = ticketOfferRes.data.application;
              const event = eventRes.data.event;
              store.dispatch("layouts/setLayout", event.layout);
              const ticketOffer = ticketOfferRes.data.ticketOffer;
              if (application.status === "pending") {
                routeTo.params.applicant = ticketOfferRes.data.applicant;
                routeTo.params.application = application;
                routeTo.params.ticketOffer = ticketOffer;
                routeTo.params.event = event;
                next();
              } else {
                next({
                  name: "ticket-offer",
                  params: {
                    eventSlug: routeTo.params.eventSlug,
                    id: ticketOffer.id
                  }
                });
              }
            })
          )
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/update-ticket-info/:id",
      name: "update-ticket-info",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-application" */ "@/ticketing/views/TicketDetails"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        TicketService.getTicketOffer(
          routeTo.params.eventSlug,
          routeTo.params.id
        )
          .then(ticketOfferRes => {
            const ticketOffer = ticketOfferRes.data.ticketOffer;
            routeTo.params.ticketOffer = ticketOffer;
            const event = ticketOfferRes.data.event;
            store.dispatch("layouts/setLayout", event.layout);
            routeTo.params.event = event;

            ProfileService.getProfile(ticketOffer.personId)
              .then(profileRes => {
                routeTo.params.person = profileRes.data.person;
                next();
              })
              .catch(() => {
                next({ name: "404" });
              });
          })
          .catch(() => {
            next({ name: "404" });
          });
      }
    },
    {
      path: "/:eventSlug/apply/offer/:id",
      name: "ticket-offer",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-application" */ "@/ticketing/views/TicketOffer"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        store
          .dispatch("ticketOffers/getTicketOffer", routeTo.params.id)
          .then(() => {
            const ticketOffer = store.getters["ticketOffers/ticketOffer"];
            if (ticketOffer.kind !== "standard") {
              const location = getLocation(ticketOffer.kind);
              next({
                name: location,
                params: {
                  id: routeTo.params.id,
                  eventSlug: routeTo.params.eventSlug
                }
              });
            } else {
              store
                .dispatch(
                  "people/getPerson",
                  store.getters["ticketOffers/ticketOffer"].personId
                )
                .then(() => {
                  store
                    .dispatch("events/getEvent", routeTo.params.eventSlug)
                    .then(() => {
                      const event = store.getters["events/event"];
                      const person = store.getters["people/person"];
                      const ticketOffer =
                        store.getters["ticketOffers/ticketOffer"];

                      next();
                    })
                    .catch(() => {
                      next({ name: "404" });
                    });
                });
            }
          });
      }
    },
    {
      path: "/:eventSlug/ticket/:id/purchase",
      name: "ticket-offer-purchase",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-application" */ "@/ticketing/views/TicketOfferPurchase"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        store
          .dispatch("ticketOffers/getTicketOffer", routeTo.params.id)
          .then(() => {
            store
              .dispatch(
                "people/getPerson",
                store.getters["ticketOffers/ticketOffer"].personId
              )
              .then(() => {
                store
                  .dispatch("events/getEvent", routeTo.params.eventSlug)
                  .then(() => {
                    const event = store.getters["events/event"];
                    const ticketOffer =
                      store.getters["ticketOffers/ticketOffer"];

                    if (ticketOffer.status === "purchased" || event.soldOut) {
                      next({
                        name: getLocation(ticketOffer.kind),
                        params: {
                          id: routeTo.params.id,
                          eventSlug: routeTo.params.eventSlug
                        }
                      });
                    } else {
                      axios
                        .all([
                          TicketService.getInvites(routeTo.params.id),
                          TicketService.getNewInvite()
                        ])
                        .then(
                          axios.spread((invitesRes, newInviteRes) => {
                            const existingInvites = invitesRes.data.invites;
                            const newInvite = newInviteRes.data.invite;

                            routeTo.params.existingInvites = existingInvites;
                            routeTo.params.newInvite = newInvite;
                            next();
                          })
                        );
                    }
                  })
                  .catch(() => {
                    next({ name: "404" });
                  });
              });
          });
      }
    },
    {
      path: "/:eventSlug/vip-invites/:id",
      name: "vip-ticket-offer",
      component: () =>
        import(/* webpackChunkName: "ticket-offer-application" */ "@/ticketing/views/VipInvite"),
      props: true,
      beforeEnter(routeTo, routeFrom, next) {
        store
          .dispatch("ticketOffers/getTicketOffer", routeTo.params.id)
          .then(() => {
            const ticketOffer = store.getters["ticketOffers/ticketOffer"];
            if (ticketOffer.kind !== "vip") {
              const location = getLocation(ticketOffer.kind);
              next({
                name: location,
                params: {
                  id: routeTo.params.id,
                  eventSlug: routeTo.params.eventSlug
                }
              });
            } else {
              store
                .dispatch(
                  "people/getPerson",
                  store.getters["ticketOffers/ticketOffer"].personId
                )
                .then(() => {
                  store
                    .dispatch("events/getEvent", routeTo.params.eventSlug)
                    .then(() => {
                      const event = store.getters["events/event"];
                      const person = store.getters["people/person"];
                      const ticketOffer =
                        store.getters["ticketOffers/ticketOffer"];

                      next();
                    })
                    .catch(() => {
                      next({ name: "404" });
                    });
                });
            }
          });
      }
    },
    {
      path: "/:eventSlug/partners/registration/:id",
      name: "partner-registration",
      component: () =>
        import(/* webpackChunkName: "partner-registration" */ "@/ticketing/views/PartnerRegistration"),
      beforeEnter(routeTo, routeFrom, next) {
        store
          .dispatch("ticketOffers/getTicketOffer", routeTo.params.id)
          .then(() => {
            const ticketOffer = store.getters["ticketOffers/ticketOffer"];
            if (ticketOffer.kind !== "partner") {
              const location = getLocation(ticketOffer.kind);
              next({
                name: location,
                params: {
                  id: routeTo.params.id,
                  eventSlug: routeTo.params.eventSlug
                }
              });
            } else {
              store.dispatch(
                "people/getPerson",
                store.getters["ticketOffers/ticketOffer"].personId
              );
              store
                .dispatch("events/getEvent", routeTo.params.eventSlug)
                .then(() => next());
            }
          });
      }
    },
    {
      path: "/404",
      name: "404",
      component: NotFound,
      props: true
    },
    {
      path: "*",
      redirect: { name: "404" }
    }
  ]
});

router.beforeEach((routeTo, routeFrom, next) => {
  NProgress.start();
  next();
});

router.afterEach(() => {
  NProgress.done();
});

function getLocation(kind) {
  return {
    standard: "ticket-offer",
    partner: "partner-registration",
    vip: "vip-ticket-offer"
  }[kind];
}

export default router;
