import NProgress from "nprogress";
import TicketService from "@/services/TicketService";

export const namespaced = true;

export const state = {
  rsvp: {}
};

export const mutations = {
  setRSVP(state, payload) {
    state.rsvp = payload;
  }
};

export const actions = {
  getRSVP({ commit }, payload) {
    NProgress.start();
    commit("setLoading", true, { root: true });
    return TicketService.getRSVP(payload.eventSlug, payload.personId).then(
      res => {
        const rsvp = res.data.rsvp;
        commit("setRSVP", rsvp);
        NProgress.done();
        commit("setLoading", false, { root: true });
      }
    );
  },

  getNewRSVP({ commit }, slug) {
    NProgress.start();
    commit("setLoading", true, { root: true });
    return TicketService.getNewRSVP(slug).then(res => {
      const rsvp = res.data.rsvp;
      commit("setRSVP", rsvp);
      NProgress.done();
      commit("setLoading", false, { root: true });
    });
  },

  createRSVP({ commit }, payload) {
    NProgress.start();
    commit("setLoading", true, { root: true });
    return TicketService.createRSVP(payload.eventSlug, payload.data)
      .then(res => {
        const rsvp = res.data.rsvp;
        commit("setRSVP", rsvp);
        NProgress.done();
        commit("setLoading", false, { root: true });
      })
      .catch(error => {
        console.log(error);
      });
  },

  updateRSVP({ commit }, payload) {
    NProgress.start();
    commit("setLoading", true, { root: true });
    return TicketService.updateRSVP(
      payload.eventSlug,
      payload.personId,
      payload.data
    )
      .then(res => {
        const rsvp = res.data.rsvp;
        commit("setRSVP", rsvp);
        NProgress.done();
        commit("setLoading", false, { root: true });
      })
      .catch(error => {
        console.log(error);
      });
  }
};

export const getters = {
  rsvp(state) {
    return state.rsvp;
  }
};
