import Vue from "vue";
import "@/plugins/axios";
import App from "./App.vue";
import VueMeta from "vue-meta";
import Vuelidate from "vuelidate";
import VueGtm from "vue-gtm";
var Rollbar = require("vue-rollbar");
import router from "./router";
import store from "@/store/store";

import Default from "@/ticketing/layouts/Default";
import Dark2019 from "@/ticketing/layouts/Dark2019";

Vue.component("default-layout", Default);
Vue.component("dark-2019-layout", Dark2019);

import "nprogress/nprogress.css";
import Buefy from "buefy";
import "@mdi/font/css/materialdesignicons.min.css";

const gtmDebug = process.env.NODE_ENV !== "production";

Vue.use(Rollbar, {
  accessToken: "d6e098d22557404e9312a287c4a7ec9c",
  captureUncaught: true,
  captureUnhandledRejections: true,
  enabled: true,
  environment: process.env.NODE_ENV,
  payload: {
    client: {
      javascript: {
        code_version: "1.0"
      }
    }
  }
});

Vue.use(VueGtm, {
  id: "GTM-WV5LPBS",
  enabled: true,
  debug: gtmDebug,
  vueRouter: router
});
Vue.use(VueMeta);

Vue.use(Buefy);

Vue.config.productionTip = false;

Vue.use(Vuelidate);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
